@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);

body{
  background-color: brown;
  background: url(/WeatherApp/static/media/background.4cbb1e73.jpg) no-repeat fixed center;
  background-size: cover;
}

.mainConent{
  display: flex;
  justify-content: center;
}

.navBar{
  margin-top: 15px;
  border: 1px solid black;
  background-color: rgba(49, 85, 87, 0.384);
  width: 300px;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 50px 40px  ;
  justify-content: center;
  align-content: center;
  border-radius: 10px;
}

.navBar h1{
  border: 0px solid black;
  font-size: 1.8rem;
  color: white;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  text-align: center;
  margin: 10px;
  padding: 0px;
  position: relative;
  z-index: 1;
  display: inline-block;
}

.busca{
  background: #f1f1f1;
    padding: 1px 5px;
    width: 170px;
    font-size: 17px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border: 1px solid white;
    border-radius: 5px;
}

.btnBusca{
    background: #1d61c7;
    padding: 1px 5px;
    margin-left: 10px;
    font-size: 17px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    border: 1px solid white;
    border-radius: 5px;
}
.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.card1 {
  background-color: #538eb6a8;
  margin: 20px;
  width: 18rem;
  border: 1px solid black;
	border-radius: 5px;
}

.card-title {
  margin-bottom: 15px;
}

#closeIcon {
  display: flex;
  justify-content: flex-end;
  margin-right: 8px;
  margin-top: 8px;
}

.card-body {
  margin-top: 0px !important;
  padding-top: 0px !important;
}

.cardImg {
	background-color: #73dbaa;
	width: 70px;
	height: 70px;
	border-radius: 15px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.App {
  text-align: center;
}

.row {
  display: flex;
  justify-content: center;
}

.dev {
  font-family: 'Special Elite', cursive;
  font-size: 1.5rem;
  color: blanchedalmond;
}
